
.str0 {
  stroke: $special-color;
  stroke-width: 0.39;
  stroke-miterlimit: 22.9256;
}

.fil1 {
  fill: #FFDBB8;
}

.fil2 {
  fill: #e6c6a7;
}

.fil0 {
  fill: $special-color;
}

.fil3 {
  fill:#6e8e99;
}

.fil4 {
  fill: #3c616d;
}

.svg {
  margin-left: 2%;
  shape-rendering:geometricPrecision;
  text-rendering:geometricPrecision;
  image-rendering:optimizeQuality;
  fill-rule:evenodd;
  clip-rule:evenodd;
  width: 5rem;
  height: 6rem;
}

.header {
  display: flex;
  align-items: center;

  &__wrap{
  background-color: aliceblue;
  }

  &__title {
    margin-left: 2rem;
    font-size: 2rem;
    font-weight: 900;
    color: $special-color
  }

  &__logo {
    display: flex;
    align-items: center;
  }
}
